body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: grid;
  grid-template-rows: auto;
  justify-content: center;
  align-items: center;
  justify-items: center;
  margin-top: 100px;
  overflow:visible;
}
html {
  font-size: 16px;
  font-family: Helvetica;
  background-color: #211D27;
}

body {

}

.album {
    width: 300px;
    height: 375px;
    /* border-style: solid;
    border-color: var(--text-color-4);
    border-width: 1px; */
    border-radius: 10px;
    background: linear-gradient(20deg, var(--bg-color) 15%, var(--bg-gradient));
    transition: all 0.5s;
    /* box-shadow: 0px 0px 20px 0px var(--shadow); */
}

.album:hover {
    box-shadow: 0px 0px 20px 0px var(--shadow);
    cursor: pointer;
}

.album img {
    margin-top: 15px;
    border-radius: 5px;
    width: 270px;
    height: 270px;
    box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, .25);
    margin-left: 15px;
}

.album-title {
    font-size: 1.25rem;
    margin-top: 12px;
    margin-bottom: 4px;
    text-align: left;
    padding-left: 15px;
    padding-right: 15px;
    font-weight: 600;
    color: var(--text-color-1);
    max-width: 300px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.album-artist {
    font-size: 1.25rem;
    margin-top: 4px;
    text-align: left;
    padding-left: 15px;
    font-weight: 400;
    color: var(--text-color-2);
}
.album-container {
    display: grid;
    grid-template-rows: 450px;
    grid-template-columns: 300px 250px 300px;
    align-content: end;
    justify-content: center;
    align-items: center;
    justify-items: center;
}

h1 {
    color: white;
    font-size: 24px;
    grid-column: span 3;
}

.skip {
    background-color: transparent;
    color: #CFCFCF;
    height: 40px;
    border: 1px solid #897ECC;
    border-radius: 20px;
    font-size: 18px;
    transition: all 0.5s;
    grid-row-start: 2;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 8px;
}

.skip:hover:enabled {
    cursor:pointer;
    background-color: #362F40;
    color: #FFFFFF
}

.skip:disabled {
    border-color: darkgrey;
    color: darkgrey;
    cursor:default;
}

.loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    background: linear-gradient(0deg, #675F99 33%, #AB9EFF 100%);
    box-sizing: border-box;
    -webkit-animation: rotation 1s linear infinite;
            animation: rotation 1s linear infinite;
  }
  .loader::after {
    content: '';  
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background: #211D27;
  }
  @-webkit-keyframes rotation {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)}
  }
  @keyframes rotation {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg)}
  } 

span {
    grid-column: span 3;
    grid-row-start: 1;
}

.skip-error-message {
    color: white;
    visibility: hidden;
    background: linear-gradient(267.35deg, #362F40 0%, rgba(54, 47, 64, 0.22) 102.71%);
    border-radius: 5px;
    width: 300px;
    height: 60px;
    line-height: 60px;
    opacity: 80%;
    transition: all 0.5s;
    text-align: center;
}

.skip-error-message:hover {
    opacity: 100%;
}

.skip-both-error {
    width: 350px;
    height: 80px;
    background: rgba(76, 66, 89, 0.8);
    border-radius: 5px;
    justify-self: center;
    align-self: center;
    z-index: 1;
    color: #CFCFCF;
    line-height: 80px;
    visibility: hidden;
    transition: opacity 2s ease-in-out 0s;
    text-align: center;
}

.skip-both-error.is-visible {
    visibility: visible;
    opacity: 0;
} 

.filters {
    grid-column: span 3;
    display: flex;
    width: 100%;
    justify-content: flex-start;
    grid-gap: 16px;
    gap: 16px;
}


