}

.calendar-wrapper {
    height: auto;
    width: 300px;
    background-color: #362F40;
    z-index: 2;
    border-radius: 20px;
    box-shadow:  4px 4px 7px #17141b,
                -4px -4px 7px #2b263390;
    border: 1px solid #897ECC00;
