}

.decade-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
}

.next-prev-button {
    width: 32px;
    height: 32px;
    margin-left: 24px;
    margin-right: 24px;
    font-size: 18px;
    background-color: #4B4259;
    border-radius: 10px;
    color: #CFCFCF;
