}

.carousal {
    overflow: hidden;
    width: 300px;
    height: auto;
}

.carousal-inner {
    white-space: nowrap;
    height: 100%;
    width: 1200px;
    display: inline-flex;
}

.inner {
    white-space: nowrap;
    height: 100%;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.carousal-item {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.indicators {
    display: flex;
    justify-content: space-between;
    padding-left: 24px;
    padding-right: 24px;
    align-items: center;
}

.decade-selector {
    font-size: 16px;
    font-weight: 600;
}

.hover {
    border: 1px solid #675F9A;
    border-radius: 20px;
}

.selected {
    background-color: #897ECC;
    border-radius: 20px;
    color: #FFFFFF;
    border: 1px solid #AB9DFF;
}

.chip-year {
    align-items: center;
